
.main-head{
    text-align: center;
    color: darkslategray;
    font-size: 60px;
    font-style: italic;
    }
    .input{
        position: relative;
        display: flex;
        width: 80%;
        margin-left: 10%;
        margin-right: 10%;
    }
    .search-bar{
        width: 100%;
        padding: 10px;
        border: 4px solid skyblue;
        outline: none;
        border-radius: 20px;
    }
    .search-bar:focus{
        border: 4px solid darkblue;
    }
    .suggestion-container{
        display: flex;

    }
    .no-results{
        text-align: center;
    }
    .no-results h4{
        color: lightslategray;
    }
    .button{
        position: absolute;
        right: 0;
    }
    .clear{
        margin: 8px;
        border: none;
        outline: none;
        font-size: 25px;
        background-color: "#fff";

    }
    .clear:hover{
        cursor: pointer;
    }
    .details{
        text-align: center;
    }
    .detailButton{
        border: 1px solid blue;
        outline: none;
        font-size: 25px;
        text-align: center;
        width: fit-content;
        height: fit-content;
        background-color: rgba(0, 0,255,0.3);
        border-radius: 4px;

    }
    .detailButton:hover{
        color: snow;
        cursor: pointer;
        background-color: rgba(0,0,255);
    }