.outerdiv{
    position:fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(173,216,230, 0.1);

}
.innerdiv{
width: 100%;
height: 100%;
animation-name: pop;
animation-duration: 5s;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}
.close{
    position: fixed;
    top: 0;
    right: 0;
    color: red;
    animation-name: fade;
    animation-duration: 5s;
}
.close:hover{
    cursor: pointer;
    text-shadow: 0px,2px,2px,red;

}
@keyframes fade{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }

}
@keyframes pop{
    from{
        margin: 40%;
        width: 20%;
        height: 20%;
    }
    to{
        margin: 0;
        width: 100%;
        height: 100%;
    }
}
table{
    border:4px solid black;
    border-spacing: 0;
    width: 70%;
    height: 80%;
    background-color: mediumslateblue;
}
tr,td{
    border: 2px solid black;
    text-align: center;
    font-size: 30px;
}
.bold{
    font-weight: bolder;
}
@media only screen and (max-width:500px){
    tr,td{
        font-size: 12px;
    }
}